.header {
    background: url(../img/header/header-desktop.jpg);

    color: #dfdfdf;
    padding: 0px;
    border: 0 solid white;
    margin: 0;
    border-top: 5px solid white;
    border-bottom: 5px solid white;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 3;

    height: 70px;

    -webkit-box-shadow: 9px 6px 44px -4px rgba(0,0,0,0.75);
    -moz-box-shadow: 9px 6px 44px -4px rgba(0,0,0,0.75);
    box-shadow: 9px 6px 44px -4px rgba(0,0,0,0.75);

}

.header .logo-title {
    font-family: playball;
    display: inline-block;
    /*text-shadow: 0 5px 8px 0 rgba(189, 189, 189, 0.2), 0 6px 20px 0 rgba(239, 239, 239, 0.19);*/
    text-shadow:4px 4px 4px #050505;
    color: white;
    font-weight: bold;
    font-size: 2.1em;
    padding-left: 5px;

    position: relative;
    top: -14px;
    left: -40px;
}

.header .logo-img {
    display: inline-block;
    padding-left: 5px;
}
